.about {
    &__prev {
        background-image: url('../img/1_page_about/about_prev_bg.jpg');
        background-repeat: no-repeat;
        background-position: top center;
        background-size: cover;
        text-align: center;
        padding: 160px 0;
        margin-top: -53px;
        &-title {
            color: $color-title;
            @extend %title-font;
            font-size: 60px;
            line-height: 80px;
        }
    }
    &__content {
        padding: 130px 0 40px;
    }
    &__title {
        @extend %title-font;
        color: $color-title;
        font-size: 36px;
        line-height: 60px;
        width: 822px;
        padding-bottom: 22px;
        margin-bottom: 22px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 200px;
            height: 2px;
            background-color: #979797;
        }
    }
    &__text {
        padding-left: 284px;
        p {
            margin-bottom: 30px;
        }
    }
    &__popup {
        background-image: url('../img/1_page_about/about_video_bg.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;
        min-height: 414px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}