$color-title: #363838;
$color-default: #5A5A5A;

%title-font {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

//    fancybox
@function max($numbers...) {
  @return m#{a}x(#{$numbers});
}
@function min($numbers...) {
  @return m#{i}n(#{$numbers});
}